export function WebTestWrapper($$$DATA$$$ = {}, systemData = {}) {
    const {App, View} = systemData;
    const __VIEW_PATH__ = 'web.test-wrapper';
    const __VIEW_ID__ = $$$DATA$$$.__SSR_VIEW_ID__ || App.View.generateViewId();
    const __VIEW_TYPE__ = 'view';
    // this is the wrapper element
    
    const __WRAPPER_ELEMENT__ = document.createElement('template');
    const __REFS__ = [];
    const self = new View.Engine();
    const __STATE__ = new View.State(self);


    const parseRefs = (frag) => {
        for (let i = 0; i < frag.childNodes.length; i++) {
            const node = frag.childNodes[i];
            __REFS__.push(node);
        }
    }
    const createHtml = (template) => {
        try {
            __WRAPPER_ELEMENT__.innerHTML = template;
        } catch (error) {
            console.error(error);
            __WRAPPER_ELEMENT__.innerHTML = '';
        }
        let frag = __WRAPPER_ELEMENT__.content;
        parseRefs(frag);
        return frag;
    }
    
    /**
     * 
     * @param {*} value 
     * @returns {[any, function, string]}
     */
    const useState = (value) => {
        return __STATE__.__useState(value);
    };
    const updateRealState = (state) => {
        __STATE__.__updateRealState(state);
    };

    const lockUpdateRealState = () => {
        __STATE__.__lockUpdateRealState();
    };
    const updateStateByKey = (key, state) => {
        __STATE__.__updateStateByKey(key, state);
    };

    if(typeof $$$DATA$$$.__SSR_VIEW_ID__ !== 'undefined'){
        delete $$$DATA$$$.__SSR_VIEW_ID__;
    }
    const __UPDATE_DATA_TRAIT__ = {};
    let {user = {"name": "John", "email": "john@example.com"}, items = ["item1", "item2", "item3"]} = $$$DATA$$$;
    __UPDATE_DATA_TRAIT__.user = value => user = value;
    __UPDATE_DATA_TRAIT__.items = value => items = value;
    const __VARIABLE_LIST__ = ["user", "items"];
    const $setUserState = __STATE__.__register('userState');
    let userState = null;
    const setUserState = (state) => {
        userState = state;
        $setUserState(state);
    };
    const update$userState = (value) => {
        if(__STATE__._canUpdateStateByKey){
            updateStateByKey('userState', value);
            userState = value;
        }
    };

    self.setup('web.test-wrapper', {
        superView: 'layouts.base',
        hasSuperView: true,
        viewType: 'view',
        sections: {
        "content":{
            "type":"long",
            "preloader":true,
            "useVars":true,
            "script":{}
        }
    },
        wrapperConfig: { enable: true },
        __props__: ["__WRAPPER_ELEMENT__", "createHtml", "__REFS__", "parseRefs"],
            __WRAPPER_ELEMENT__: __WRAPPER_ELEMENT__,
            refs: __REFS__,
            states: __STATE__,
            parseRefs: parseRefs,
            createHtml: createHtml,
        hasAwaitData: true,
        hasFetchData: false,
        subscribe: ["userState"],
        fetch: null,
        data: $$$DATA$$$,
        viewId: __VIEW_ID__,
        path: __VIEW_PATH__,
        usesVars: true,
        hasSections: true,
        hasSectionPreload: true,
        hasPrerender: true,
        renderLongSections: ["content"],
        renderSections: ["content"],
        prerenderSections: ["content"],
        userDefined: {},
        resources: [],
        commitConstructorData: function() {
            // Then update states from data
            update$userState(user);
            // Finally lock state updates
            lockUpdateRealState();
        },
        updateVariableData: function(data) {
            // Update all variables first
            for (const key in data) {
                if (data.hasOwnProperty(key)) {
                    this.updateVariableItem(key, data[key]);
                }
            }
            // Then update states from data
            update$userState(user);
            // Finally lock state updates
            lockUpdateRealState();
        },
        updateVariableItem: function(key, value) {
            this.data[key] = value;
            if (typeof __UPDATE_DATA_TRAIT__[key] === "function") {
                __UPDATE_DATA_TRAIT__[key](value);
            }
        },
        loadServerData: function() {
    
},
        prerender: function() {
    let {user = {"name":"John","email":"john@example.com"}, items = ["item1","item2","item3"]} = __$spaViewData$__ || {};
    
    let __outputRenderedContent__ = '';
            try {
                __outputRenderedContent__ = `
${App.View.section('content', `<div class="data-preloader" ref="${__VIEW_ID__}" data-view-name="${__VIEW_PATH__}">${App.View.text('loading')}</div>`, 'html')}
`;
            } catch(e) {
                __outputRenderedContent__ = this.__showError(e.message);
                console.warn(e);
            }
            return this.__extends('layouts.base');
            },
        virtualPrerender: function() {
    let {user = {"name":"John","email":"john@example.com"}, items = ["item1","item2","item3"]} = __$spaViewData$__ || {};
    
    let __outputRenderedContent__ = '';
            try {
                __outputRenderedContent__ = `
${this.__sectionScan('content', `<div class="data-preloader" ref="${__VIEW_ID__}" data-view-name="${__VIEW_PATH__}">${this.__textScan('loading')}</div>`, 'html')}
`;
            } catch(e) {
                __outputRenderedContent__ = this.__showErrorScan(e.message);
                console.warn(e);
            }
            return this.__extendsScan('layouts.base');
            },
        render: function() {
                
    let __outputRenderedContent__ = '';
    let __junkContent__ = '';
    try {
        __junkContent__ = `${App.View.execute(() => { if(!userState){ return `
${App.View.section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}
`; } else if(userState['name'] === 'John'){ return `
${App.View.section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}
`; } else { return `
${App.View.section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}
`; }
return '';
})}`;
    } catch(e) {
        // Ignore junk content errors
    }
            try {
                __outputRenderedContent__ = `${this.__block('document.body', {}, `<div class="container">
<h1>Test @subscribe State</h1>
<h3>User</h3>
${this.__follow(["userState"], () => `
${App.View.renderView(this.__include("web.test-subscribe-state-user", {}))}
${App.View.renderView(this.__includeif('web.test-subscribe-state-user', userState))}
${App.View.renderView(this.__includewhen(userState['name'] === 'John', "web.test-subscribe-state-user", userState))}
${this.__section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}

<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>
`)}
<h3>Items</h3>
<div class="items-list">
${App.View.foreach(items, (item, __loopKey, __loopIndex, __loop) => `
<p >${App.View.escString(item)}</p>
`)}
</div>
<h3>Items following</h3>
${this.__follow(["items"], () => `
<div class="items-list">
${App.View.foreach(items, (item, __loopKey, __loopIndex, __loop) => `
<p >${App.View.escString(item)}</p>
`)}
</div>
`)}
</div>`)}
`;
            } catch(e) {
                __outputRenderedContent__ = this.__showError(e.message);
                console.warn(e);
            }
            return this.__extends('layouts.base');
            },
        virtualRender: function() {

    let __outputRenderedContent__ = '';
    let __junkContent__ = '';
    try {
        __junkContent__ = `${App.View.execute(() => { if(!userState){ return `
${App.View.section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}
`; } else if(userState['name'] === 'John'){ return `
${App.View.section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}
`; } else { return `
${App.View.section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}
`; }
return '';
})}`;
    } catch(e) {
        // Ignore junk content errors
    }
    try {
        __outputRenderedContent__ = `${this.__blockScan('document.body', {}, `<div class="container">
<h1>Test @subscribe State</h1>
<h3>User</h3>
${this.__followScan(["userState"], () => `
${App.View.renderView(this.__include("web.test-subscribe-state-user", {}))}
${App.View.renderView(this.__includeif('web.test-subscribe-state-user', userState))}
${App.View.renderView(this.__includewhen(userState['name'] === 'John', "web.test-subscribe-state-user", userState))}
${this.__section('content', `<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>`, 'html')}

<div class="user-info" >
<p>Name: ${App.View.escString(userState['name'])}</p>
<p>Email: ${App.View.escString(userState['email'])}</p>
</div>
`)}
<h3>Items</h3>
<div class="items-list">
${App.View.foreach(items, (item, __loopKey, __loopIndex, __loop) => `
<p >${App.View.escString(item)}</p>
`)}
</div>
<h3>Items following</h3>
${this.__followScan(["items"], () => `
<div class="items-list">
${App.View.foreach(items, (item, __loopKey, __loopIndex, __loop) => `
<p >${App.View.escString(item)}</p>
`)}
</div>
`)}
</div>`)}
`;
    } catch(e) {
        __outputRenderedContent__ = this.__showErrorScan(e.message);
        console.warn(e);
    }
    return this.__extendsScan('layouts.base');
},
        init: function() {  },
        destroy: function() {},
        addCSS: function() {
            // No CSS content found in @onInit
        },
        removeCSS: function() {
            const styles = document.querySelectorAll('style[data-view-name="web.test-wrapper"]');
            styles.forEach(style => style.remove());
        }

    });
    return self;
        }